import s from './ContactItem.module.css'

export default function ContactItem({ contact, deleteContact }) {
  const handleClick = (e) => deleteContact(e.target.name)
  const { id, name, number } = contact

  return (
    <li key={id}>
      {name}: {number}
      <button
        className={s.btnDel}
        type="button"
        name={name}
        onClick={handleClick}
      >
        Delete
      </button>
    </li>
  )
}
